import React from "react";
import { Doughnut } from "react-chartjs-2";
import "./Head.css";
import { useState, useEffect } from "react";
import axios from "axios";

export default function Piecharts() {
  const [radCount, setradCount] = useState([]);
  const [internCount, setinternCount] = useState([]);

  useEffect(() => {
    const one = "http://54.242.160.154:8080/api/RAD/radInternCount";
    const two = "http://54.242.160.154:8080/api/RAD/radInternCount";

    const requestOne = axios.get(one, { params: { role: "RAD engineer" } });
    const requestTwo = axios.get(two, { params: { role: "RADIntern" } });

    axios
      .all([requestOne, requestTwo])
      .then(
        axios.spread((...responses) => {
          const responseOne = responses[0].data.radMembers;
          const responseTwo = responses[1].data.radMembers;

          setradCount(responseOne);
          setinternCount(responseTwo);
          console.log(responseOne, responseTwo);
        })
      )
      .catch((errors) => {
        console.error(errors);
      });
  }, []);

  return (
    <div>
      <div className="doughnut-container">
        <canvas id="doug"></canvas>
        <Doughnut
          type="Doughnut"
          data={{
            datasets: [
              {
                data: [radCount, internCount],
                backgroundColor: [" #525252", " #FF0000"],
              },
            ],
          }}
          options={{
            title: {
              display: true,
              text: "Interns vs RAD Engineers",
              fontSize: 20,
            },
            legend: {
              display: true,
            },
          }}
        />
      </div>
      {/* <p>Intern Vs Rad Engineer Ratio</p> */}
      {/* <div className="pielabel1">Intern</div>
        <div className="pielabel2">vs</div>
        <div className="pielabel3">RAD Engineer</div>
        <div className="pielabel4">Ratio</div> */}
    </div>
  );
}
