import './Head.css';
import React from 'react';
import axios from 'axios';
import { useState,useEffect } from 'react';

function HeadCount() {
  const [headCount,setheadCount]=useState([]); 
  const [radCount,setradCount]=useState([]); 
  const [internCount,setinternCount]=useState([]); 
  const [foulCount,setfoulCount]=useState([]); 
  useEffect(() => {
    const one =
  'http://54.242.160.154:8080/api/RAD/totalRAD';
  const two =
  'http://54.242.160.154:8080/api/RAD/radInternCount';
  const three =
  'http://54.242.160.154:8080/api/RAD/radInternCount';
  const four =
  'http://54.242.160.154:8080/api/RAD/radInternCount';
const requestOne = axios.get(one,{ params: { department:"RAD" }});
const requestTwo = axios.get(two,{ params: { role:"RAD engineer" } });
const requestThree = axios.get(three,{ params: { role:"RADIntern" } });
const requestFour = axios.get(four,{ params: { role:"Furlough"} });
axios
  .all([requestOne, requestTwo, requestThree,requestFour])
  .then(
    axios.spread((...responses) => {
      const responseOne = responses[0].data.totalRADians;
      const responseTwo = responses[1].data.radMembers;
      const responesThree = responses[2].data.radMembers;
      const responesFour = responses[3].data.radMembers;
      setheadCount(responseOne);
      setradCount(responseTwo) ;
      setinternCount(responesThree);
      setfoulCount(responesFour);
      //console.log(responseOne);
    })
  )
  .catch(errors => {
    console.error(errors);
  });
},[])
  return (
    <div>
      <div className="hd">Head Count</div>
      <div className="headcounter">
        <text className="text1">{headCount}</text>
        <text className="text2">Head Count</text>
      </div>
      <div className="radf">
        <text className="rad1">{radCount}</text>
        <text className="rad2">Rad Engineers</text>
      </div>
      <div className="interns">
        <text className="interns1">{internCount}</text>
        <text className="interns2">Interns</text>
      </div>
      <div className="foul">
        <text className="foul1">{foulCount}</text>
        <text className="foul2">Fouloughed</text>
      </div>
    </div>
        );
    }
export default HeadCount;
