import React, { useState, useContext } from "react";
import { Formik, useFormik } from "formik";
import * as Yup from "yup";
import "./login.css";
import axios from "axios";
import TextField from "@material-ui/core/TextField";
import { Button, createStyles, Link, Theme } from "@material-ui/core";
import { userContext } from "../Providers/userContext";
import { useHistory } from "react-router-dom";
import { API_URL } from "../Shared/urls";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";

const validationSchema = Yup.object({
  email: Yup.string()
    .email("Enter a valid email")
    .required("Email is required"),
  password: Yup.string()
    .min(8, "Password should be of minimum 8 characters length")
    .required("Password is required"),
});
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary,
  },
}));
const Login = () => {
  const classes = useStyles();

  const { user, setUser } = useContext(userContext);
  const [error, setError] = useState("");
  console.log(user);
  const history = useHistory();
  const formik = useFormik({
    initialValues: {
      email: "",
      password: "",
    },
    validationSchema: validationSchema,
    onSubmit: (values: { email: any; password: any }) => {
      setError("");
      const payload = {
        username: values.email,
        password: values.password,
      };
      axios
        .post(API_URL + "/signin", payload, {
          headers: {
            "content-type": "application/json ",
            "Access-Control-Allow-Origin": "*",
          },
        })
        .then((res: any) => {
          if (res) {
            setUser(res);
            localStorage.removeItem("token");
            localStorage.setItem("token", res.data.accessToken);
            history.push("/");
          }
        })
        .catch((err: any) => {
          setError("Something went wrong");
          console.log(err);
        });
    },
  });
  const styles = {
    paperContainer: {
      backgroundImage: `url(${"/image1.png"})`,
    },
  };
  return (
    <div className="App background">
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <div _ngcontent-pop-c53="" className="logo-container">
            <img
              src={process.env.PUBLIC_URL + "image3.png"}
              className=""
              alt="logo"
            />
          </div>
        </Grid>
        <Grid item xs={12}></Grid>
        <Grid item xs={8}>
          <div className="textwhat">What's Next ?</div>
        </Grid>
        <Grid item xs={7}></Grid>
        <Grid item xs={5}>
          <div>
            <form className="" onSubmit={formik.handleSubmit}>
              <Grid container spacing={3}>
                <Grid item xs={12}>
                  <TextField
                    type="text"
                    id="outlined-basic"
                    variant="outlined"
                    placeholder="User Name"
                    margin="dense"
                    name="email"
                    box-sizing="border-box"
                    value={formik.values.email}
                    onChange={formik.handleChange}
                    error={formik.touched.email && Boolean(formik.errors.email)}
                    helperText={formik.touched.email && formik.errors.email}
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    type="text"
                    id="outlined-basic "
                    variant="outlined"
                    name="password"
                    placeholder="Password"
                    margin="dense"
                    box-sizing="border-box"
                    value={formik.values.password}
                    onChange={formik.handleChange}
                    error={
                      formik.touched.password && Boolean(formik.errors.password)
                    }
                    helperText={
                      formik.touched.password && formik.errors.password
                    }
                  />
                </Grid>
                <Grid item xs={6}>
                  <div className="iconremember">
                    <input
                      type="checkbox"
                      value="lsRememberMe"
                      id="rememberMe"
                    />{" "}
                    <label htmlFor="rememberMe">Remember me </label>
                  </div>
                </Grid>
                <Grid item xs={6}>
                  <div className="iconforget">
                    <Link href={"/ForgetPass"} className="forgot-pass">
                      Forgot password?
                    </Link>
                  </div>
                </Grid>
                <Grid item xs={12}>
                  <p className="error">{error}</p>
                  <Button
                    variant="contained"
                    type="submit"
                    className="btn-login"
                  >
                    Log in
                  </Button>
                </Grid>
              </Grid>
            </form>
          </div>{" "}
        </Grid>
      </Grid>
    </div>
  );
};
export default Login;
