import "./sidebar.css";
import PowerSettingsNewIcon from "@material-ui/icons/PowerSettingsNew";
import SettingsIcon from "@material-ui/icons/Settings";
import React, { useContext, useState } from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import axios from "axios";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import { userContext } from "../Providers/userContext";

function Header() {
  const { user, setUser } = useContext(userContext);
  const [oldPass, setoldPass] = useState("");
  const [newPass, setnewPass] = useState("");
  const [confirmPass, setconfirmPass] = useState("");
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClickClose = () => {
    setOpen(false);
    setoldPass("");
    setnewPass("");
    setconfirmPass("");
  };

  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event: any) => {
    setAnchorEl(event.currentTarget);
    //setAnchorEl(null);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleSave = () => {
    axios
      .get(
        "https://9eed0692-e128-410f-b4f1-6beac51fbabc.mock.pstmn.io/api/change/change-password?"
      )
      .then((res: { data: String }) => {
        console.log(res.data);
      });
    setOpen(false);
    setoldPass("");
    setnewPass("");
    setconfirmPass("");
    alert("Password Changed Successfully");
    console.log(oldPass, newPass, confirmPass);
  };
  const logoOut = () => {
    localStorage.removeItem("token");
    window.location.href = "/login";
    setUser(null);
  };

  return (
    <div>
      <div className="logo">
        <img
          src={process.env.PUBLIC_URL + "/B! logo Box 1.png"}
          className="App-logo"
          alt="logo"
        />
        <div className="settings1">
          {" "}
          <PowerSettingsNewIcon onClick={logoOut}></PowerSettingsNewIcon>{" "}
        </div>
        <div className="settings2">
          <SettingsIcon
            aria-controls="simple-menu"
            aria-haspopup="true"
            onClick={handleClick}
          ></SettingsIcon>
          <Menu
            id="simple-menu"
            anchorEl={anchorEl}
            keepMounted
            open={Boolean(anchorEl)}
            onClose={handleClose}
          >
            <MenuItem onClick={handleClickOpen}>Change Password</MenuItem>
            <MenuItem onClick={handleClose}>Logout</MenuItem>
          </Menu>

          <Dialog
            open={open}
            onClose={handleClickClose}
            aria-labelledby="form-dialog-title"
          >
            <DialogTitle id="form-dialog-title">Change Password</DialogTitle>
            <DialogContent>
              <TextField
                name="oldPass"
                value={oldPass}
                //value={values.oldPass}
                onChange={(e) => setoldPass(e.target.value)}
                autoFocus
                margin="dense"
                id="old"
                label="Old Password"
                type="password"
                fullWidth
              />

              <TextField
                name="newPass"
                value={newPass}
                //value={values.newPass}
                onChange={(e) => setnewPass(e.target.value)}
                margin="dense"
                id="new"
                label="New Password"
                type="password"
                fullWidth
              />

              <TextField
                name="confirmPass"
                value={confirmPass}
                //value={values.confirmPass}
                onChange={(e) => setconfirmPass(e.target.value)}
                margin="dense"
                id="confirm"
                label="Confirm Password"
                type="password"
                fullWidth
              />
            </DialogContent>
            <DialogActions>
              <Button onClick={handleClickClose}>Cancel</Button>

              <Button onClick={handleSave}>Save</Button>
            </DialogActions>
          </Dialog>
        </div>
      </div>
    </div>
  );
}
export default Header;
