import React from "react";
import "./Head.css";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import { useState, useEffect } from "react";
import axios from "axios";
const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: "#525252",
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    "&:nth-of-type(odd)": {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

const useStyles = makeStyles({
  table: {
    minWidth: 700,
    minHeight: 100,
  },
});

export default function BasicTable() {
  const classes = useStyles();
  const [radEmployeeList, setData] = useState([]);

  useEffect(() => {
    console.log(radEmployeeList);
    if (radEmployeeList.length != 0) {
      return;
    }
    axios
      .get("http://54.242.160.154:8080/api/RAD/searchRADDetails", {
        params: { department: "RAD" },
      })
      .then((res: any) => {
        setData(res.data.radTeamDetails);
      })
      .catch((err) => {
        console.log(err);
      });
  });

  return (
    <div className="tablecontainer">
      <TableContainer component={Paper}>
        <Table className={classes.table} aria-label="customized table">
          <TableHead>
            <TableRow>
              <StyledTableCell align="right"></StyledTableCell>
              <StyledTableCell align="right"></StyledTableCell>
              <StyledTableCell align="right">Name</StyledTableCell>
              <StyledTableCell align="right">Role</StyledTableCell>
              <StyledTableCell align="right">RAD &nbsp; ID</StyledTableCell>
              <StyledTableCell align="right">
                Joining &nbsp; Date{" "}
              </StyledTableCell>
              <StyledTableCell align="right">Department</StyledTableCell>
              <StyledTableCell align="right">Project</StyledTableCell>
              <StyledTableCell align="right">Salary</StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {radEmployeeList.map((item: any, index) => (
              <StyledTableRow key={item.emp_id}>
                <StyledTableCell component="th" scope="row"></StyledTableCell>
                <StyledTableCell align="right">
                  <img
                    src={`data:image/png;base64,${item.photo}`}
                    width="50"
                    height="50"
                  ></img>
                </StyledTableCell>
                <StyledTableCell align="right">{item.name}</StyledTableCell>
                <StyledTableCell align="right">
                  {item.roleStatus}
                </StyledTableCell>
                <StyledTableCell align="right">{item.radID}</StyledTableCell>
                <StyledTableCell align="right">
                  {item.joiningDate}
                </StyledTableCell>
                <StyledTableCell align="right">
                  {item.department}
                </StyledTableCell>
                <StyledTableCell align="right">{item.Project}</StyledTableCell>
                <StyledTableCell align="right">{item.salary}</StyledTableCell>
              </StyledTableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
}
