import React from "react";
import { Bar } from "react-chartjs-2";
import "./Head.css";
import { useState, useEffect } from "react";
import axios from "axios";

export default function Charts() {
  const [ux, setUx] = useState([]);
  const [mobile, setMobile] = useState([]);
  const [back, setBack] = useState([]);
  const [front, setFront] = useState([]);
  useEffect(() => {
    axios
      .get("http://54.242.160.154:8080/api/RAD/roleStatusBar", {
        params: { department: "RAD" },
      })
      .then((res: any) => {
        setUx(res.data.UX);
        setMobile(res.data.mobileDevelopers);
        setBack(res.data.backendDevelopers);
        setFront(res.data.frontendDevelopers);
        //console.log(role)
      })

      .catch((err) => {
        console.log(err);
      });
  }, []);

  return (
    <div className="chart-container">
      <canvas id="chart"></canvas>
      <Bar
        type="bar"
        data={{
          labels: ["UX design", "Mobile", "Backend", "Frontend"],
          datasets: [
            {
              label: "Rad Team Count by Role Description",
              backgroundColor: " #C4C4C4",
              borderColor: "rgba(0,0,0,1)",
              borderWidth: 2,
              borderRadius: 6,
              data: [ux, mobile, back, front],
            },
          ],
        }}
        options={{
          title: {
            display: true,
            text: "Rad Team Count by Role Description",
            fontSize: 20,
          },
          legend: {
            display: true,
            position: "right",
          },
        }}
      />
    </div>
  );
}
